ul {
    list-style-type: none;
    padding: 0;

}

a {
    text-decoration: none;
    color: inherit;
}

.carousel__wrap {
    /*margin-top: 10%;*/
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    width: 80%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.carousel__inner {
    height: 50rem;
    position: relative;

    width: calc(80rem);
}

.carousel__container {
    height: 100%;
    overflow: hidden;
    position: relative;
    width: 100%;

}

.carousel__slide-list {
    height: 100%;
    left: 50%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: absolute;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: calc(330rem);
}@media (max-width: 768px) {
    .carousel__slide-list {
        height: 100%;
        left: 50%;
        list-style-type: none;
        margin: 0;
        padding: 0;
        position: absolute;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        width: calc(318rem);
    }
}



.carousel__slide-item {
    display: inline-block;
    height: 36rem;
    margin: 0;
    /*box-shadow: 0 1rem 1rem -1rem rgb(1, 191, 113);*/

    padding: 1rem;

    position: absolute;
    transition: all 0.3s;
    width: 30rem;
}.carousel__slide-item.active {
    display: inline-block;
    height: 28rem;
    margin-top: 7rem;
    padding: 0 0rem 5rem;
    position: absolute;
    transition: all 0.3s;
    width: 30rem;

}@media (max-width: 768px) {
    .carousel__slide-item {
        display: inline-block;
        height: 28rem;
        margin-top: 7rem;
        padding: 0 0rem 5rem;
        position: absolute;
        transition: all 0.3s;
        width: 18rem;
    }
}


.carousel__slide-item-img-link {
    /*cursor: zoom-in;*/
    display: -webkit-flex;
    display: flex;
    height: 100%;
    overflow: hidden;
    position: relative;
    width: 100%;
}
.carousel__slide-item-img-link img {
    height: 100%;
    object-fit: contain;
    transition: all 0.5s ease;
    width: 100%;


}
.carousel__slide-item-img-link::after {
    -webkit-align-items: center;
            align-items: center;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    /*content: "read more";*/
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-justify-content: center;
            justify-content: center;

    opacity: 1;
    position: absolute;
    transition: all 0.5s ease;
    width: 100%;
}
.carousel__slide-item-img-link:hover::after {
    /*opacity: 1;*/
}
.carousel__slide-item-img-link:hover img {
    /*transform: scale(1.3);*/
}


