.carousel-slide-item__body h4 {
    font-size: 1.7rem;
    margin: 0.7rem 0 0;
    text-transform: uppercase;
    text-align: center;
    color: #FFFFFF;

    font-family: Imported,sans-serif;

}
.carousel-slide-item__body p {
    font-size: 1.1rem;
    margin: 0.7rem 0 0;
    color: #FFFFFF;
    height: 100%;
    text-align: center;
    font-family: MontserratThin,sans-serif;
}

.carousel__btn {
    -webkit-align-items: center;
            align-items: center;
    background: 0;
    border: 0;
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.carousel__btn--prev {
    left: -9%;
}
.carousel__btn--next {
    right: -9%;

}

.carousel__btn-arrow {
    border: solid #1E1E1E;
    border-width: 0 0.3rem 0.3rem 0;
    height: 2rem;
    padding: 1px;
    width: 2rem;
    z-index: 10;
}
.carousel__btn-arrow:hover {
    border: solid rgb(0, 196, 142);
    border-width: 0 0.3rem 0.3rem 0;
    /*box-shadow: 0 2px 6px rgb(0, 196, 142);*/

}
.carousel__btn-arrow--left {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
.carousel__btn-arrow--right {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.carousel__dots {
    /*display: inline-block;*/
    left: 50%;
    margin-top: -4%;
    position: absolute;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}@media (max-width: 768px) {
    .carousel__dots {
        /*display: inline-block;*/
        left: 50%;
        margin-top: -26%;
        position: absolute;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
}
.carousel__dots .dot {
    background: #1E1E1E;
    border: 0;
    border-radius: 50%;
    cursor: pointer;
    height: 1.5rem;
    margin: 0 0.3rem;
    outline: none;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    width: 1.5rem;
}@media (max-width: 768px) {
    .carousel__dots .dot {
        background: #1E1E1E;
        border: 0;
        border-radius: 50%;
        cursor: pointer;
        height: 1.4rem;
        margin: 0 0.1rem;
        outline: none;
        -webkit-transform: scale(0.3);
                transform: scale(0.3);
        width: 1.4rem;
    }
}


.carousel__dots .dot.active {
    background: rgb(1, 191, 113);

}

