.wrapper {
    background-color: #1E1E1E;
    width: 700px;
    padding: 2rem;
    box-shadow: 0 1.5rem 1rem -1rem rgb(1, 191, 113);
    border-radius: 0.3rem;
}

.input {

    width: 250px;
    height: 45px;
    padding: 0 1rem;
    margin-top: 1rem;
    box-sizing: border-box;
    font: inherit;
    font-family: MontserratThin,sans-serif;

    border-radius: 50px;
    /*border: 2px solid rgb(1, 191, 113);*/
    /*color: rgb(1, 191, 113);*/
    -webkit-appearance: none;
}

.button {
    -webkit-appearance: none;

    border-width: 1px;
    border-style: solid;
    border-color: rgb(1, 191, 113) rgb(1, 191, 113) rgb(1, 191, 113);
    border-image: none;
    border-image: initial;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin-top: 10%;
    /*font-family: Imported, serif;*/
    padding: 12px 30px;
    border-radius: 50px;
    background: rgba(1,191,113,0);
    font-family: Montserrat, serif;
    font-size: 16px;

    color: rgb(1, 191, 113);
/*    background: ${({primary}) => (primary ? 'rgba(1,191,113,0)' : '#1E1E1E')};*/
/*padding: ${({big}) => (big ? '14px 48px' : '12px 30px')};*/
