/*font-family: Imported, sans-serif;*/
/*justify-content: center;*/
/*align-items: center;*/
/*transition: all 0.2s ease-in-out;*/
}
.button:hover {
    transition: all 0.2s ease-in-out;
    background: #FFFFFF;
    color: #1E1E1E;

}



.input:focus {
    border-color: rgb(1, 191, 113);
    outline: none;
}

.input.has-error {
    border-color: tomato;
}

.error {
    margin: 0;
    font-size: 90%;
    color: tomato;
}

.tag-item {
    background-color: #d4d5d6;
    display: inline-block;
    font-size: 14px;
    border-radius: 30px;
    height: 30px;
    padding: 0 4px 0 1rem;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
    margin: 0 0.3rem 0.3rem 0;
}

.tag-item > .button {
    background-color: white;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    font: inherit;
    margin-left: 10px;
    font-weight: bold;
    padding: 0;
    line-height: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

